.rv-sidebar {
  .sidebar-toggle-btn-wrap {
    position: absolute;
    bottom: 20px;
    left: 2px;
  }

  .logo-wrap {
    text-align: center;

    img {
      width: 172px;
    }
  }

  .rs-sidenav-subtle {
    background: transparent;
    margin-top: 45px;
  }

  .sidebar-custom-icon {
    font-size: 16px;
    margin-right: 20px;
    position: absolute;
    left: 20px;
    top: 17px;
    line-height: 1.25;
  }

  .dashboard-icon path {
    fill: #000000;
  }

  .properties-icon path {
    fill: #000000;
  }

  .offer-icon path {
    fill: #000000;
  }

  .pie-chart-icon path {
    fill: #000000;
  }

  .settings-icon path {
    fill: #000000;
  }
}
