// imputBox1 css
.input-Box-1 {
  height: 40px;
  width: 100%;
  display: flex;
  border-radius: 5px 5px 5px 5px;

  .symbol {
    background-color: rgb(228, 231, 235);
    height: 100%;
    width: 10%;
    text-align: center;
    padding-top: 10px;
    border-radius: 5px 0 0 5px;
    border-top: 1px solid lightgrey;
    border-left: 1px solid lightgrey;
    border-bottom: 1px solid lightgrey;
    border-color: grey;
  }

  input[type="text"] {
    height: 100%;
    width: 82.5%;
    border-radius: 0 5px 5px 0;
    border-width: 1px;
    border-color: lightgrey;
    text-align: right;
  }
}

#Year {
  text-align: left;
}

// imputBox2 css
.input-Box-2 {
  height: 40px;
  width: 100%;
  display: flex;
  border-radius: 5px 5px 5px 5px;

  input[type="text"] {
    height: 100%;
    width: 72.5%;
    border-radius: 5px 0 0 5px;
    border-width: 1px;
    border-color: lightgrey;
    text-align: right;
    outline: none;
  }

  input[type="button"] {
    width: 10%;
    height: 100%;
    outline: none;
  }

  .button2 {
    border-radius: 0 5px 5px 0;
  }

  .active {
    background-color: #727cf5;
    color: white;
    border-width: 2px;
    border-color: #acb3ff;
    border-style: solid;
  }

  .non-active {
    background-color: white;
    color: #727cf5;
    border-width: 1px;
    border-color: #727cf5;
  }
}

// imputBox3 css
.input-Box-3 {
  height: 40px;
  width: 92.5%;

  input[type="button"] {
    width: 50%;
    height: 100%;
    outline: none;
  }

  .button1 {
    border-radius: 5px 0 0 5px;
  }

  .button2 {
    border-radius: 0 5px 5px 0;
  }

  .active {
    background-color: #727cf5;
    color: white;
    border-width: 2px;
    border-color: #acb3ff;
    border-style: solid;
  }

  .non-active {
    background-color: white;
    color: #727cf5;
    border-width: 1px;
    border-color: #727cf5;
  }
}

//inputDatePicker css
.InputDatePicker-container {
  width: 92.5%;
  height: 100%;
  position: relative;

  .react-datepicker-wrapper {
    height: 100%;
    width: 100%;

    .react-datepicker__input-container {
      height: 40px;

      input[type="text"] {
        width: 100%;
        height: 100%;
        outline: none;
        border-width: 1px;
        border-radius: 5px 5px 5px 5px;
        border-color: lightgrey;
      }
    }
  }

  .calendar {
    position: absolute;
    right: 15px;
    top: 10px;
    font-size: 20px;
  }
}
