.top-nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  background: #ffffff;
  border-bottom: 1px solid #dddddd;

  .container-fluid {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }

  .right {
    display: flex;
    align-items: center;
  }

  .notification-icon {
    margin-left: 15px;
    margin-top: 5px;

    path {
      fill:rgb(110,118,142);
    }
  }

  .user {
    display: flex;
    align-items: center;
    margin-left: 30px;
    padding-left: 10px;
    border-left: 1px solid #dddddd;

    .icon {
      width: 32px;
      height: 32px;
      background-color: #ffbc00;
      border-radius: 50%;
    }

    .name {
      margin-left: 10px;
      color: #86959d;
      font-size: 14px;
    }
  }
}


